* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
@font-face {
  font-family: "digital-7regular";
  src: url("digital-7.regular-webfont.woff2") format("woff2"),
    url("digital-7.regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
body {
  background-color: #daba09;
}
.heading {
  margin: auto;
  width: 50%;
  text-align: center;
  padding: 20px;
  font-family: digital-7regular;
  font-size: 9em;
  color: #0275e9;
  margin: 60px 20px 20px 300px;

  text-shadow: 5px 5px 5px rgb(1, 22, 61);
}
.clock {
  margin-left: 550px;
  margin-right: 550px;
  margin-top: 70px;
  width: 150px;
  height: 150px;
  border: 1px rgb(1, 22, 61);
  border-style: dotted;
  border-radius: 15px 15px;
}
